import { Component, Prop, Event, EventEmitter, h } from '@stencil/core';

export type EmptyStateIllustrationSize = 'sm' | 'md' | 'lg';

@Component({
  tag: 'bh-empty-state',
  styleUrl: 'bh-empty-state.css',
  shadow: true,
})
export class BhEmptyState {
  /**
   * Title text
   */
  @Prop() emptyTitle: string = 'No items found';

  /**
   * Description text
   */
  @Prop() description: string = 'There are no items to display at the moment.';

  /**
   * Size of the illustration
   */
  @Prop() illustrationSize: EmptyStateIllustrationSize = 'md';

  /**
   * Path to the illustration SVG
   */
  @Prop() illustrationPath: string = '';

  /**
   * Primary action button text
   */
  @Prop() primaryActionText: string = '';

  /**
   * Primary action icon name
   */
  @Prop() primaryActionIcon: string = '';

  /**
   * Secondary action button text
   */
  @Prop() secondaryActionText: string = '';

  /**
   * Secondary action icon name
   */
  @Prop() secondaryActionIcon: string = '';

  /**
   * Max width of the component
   */
  @Prop() maxWidth: string = '512px';

  /**
   * Emitted when primary action is clicked
   */
  @Event() bhPrimaryAction!: EventEmitter<void>;

  /**
   * Emitted when secondary action is clicked
   */
  @Event() bhSecondaryAction!: EventEmitter<void>;

  private handlePrimaryAction = (): void => {
    this.bhPrimaryAction.emit();
  };

  private handleSecondaryAction = (): void => {
    this.bhSecondaryAction.emit();
  };

  private getIllustrationDimensions(): { width: string; height: string } {
    switch (this.illustrationSize) {
      case 'sm':
        return { width: '120px', height: '120px' };
      case 'md':
        return { width: '200px', height: '200px' };
      case 'lg':
        return { width: '280px', height: '280px' };
      default:
        return { width: '200px', height: '200px' };
    }
  }

  render() {
    const dimensions = this.getIllustrationDimensions();
    const hasActions = this.primaryActionText || this.secondaryActionText;

    return (
      <div
        class="empty-state"
        style={{ maxWidth: this.maxWidth }}
        part="container"
      >
        <div class="empty-state-content">
          {/* Illustration */}
          {this.illustrationPath && (
            <div class="empty-state-illustration">
              <img
                src={this.illustrationPath}
                alt="Empty state illustration"
                style={{ width: dimensions.width, height: dimensions.height }}
              />
            </div>
          )}

          {/* Text Content */}
          <div class="empty-state-text">
            <h3 class="empty-state-title">{this.emptyTitle}</h3>
            <p class="empty-state-description">{this.description}</p>
          </div>
        </div>

        {/* Actions */}
        {hasActions && (
          <div class="empty-state-actions">
            {this.secondaryActionText && (
              <bh-button
                hierarchy="secondary"
                size="md"
                label={this.secondaryActionText}
                icon={this.secondaryActionIcon ? 'leading' : 'none'}
                iconName={this.secondaryActionIcon}
                onBhClick={this.handleSecondaryAction}
              />
            )}
            {this.primaryActionText && (
              <bh-button
                hierarchy="primary"
                size="md"
                label={this.primaryActionText}
                icon={this.primaryActionIcon ? 'leading' : 'none'}
                iconName={this.primaryActionIcon}
                onBhClick={this.handlePrimaryAction}
              />
            )}
          </div>
        )}
      </div>
    );
  }
}
